export default {
	data() {
		return {
			PageCur: '我的',
			userinfo:{},
			severList:[
				[
					{
						name:'字体设置',
						url: '/pages/index/Personal-center/Font-size',
					},
					{
						name:'关于龙井人家',
						url: '/pages/index/Personal-center/About',
					}
				]
			],
		};
	},
	onLoad() {
		//加载
		this.init();
	},
	methods: {
		init() {
			//用户信息
			this.userinfo={
				face:'../../static/HM-PersonalCenter/face.jpeg',
				username:"李明明",
				integral:"14355900"
			}		
		},
		//用户点击订单类型
		toOrderType(index){
			uni.showToast({title: this.orderTypeLise[index].name});
		},
		//用户点击列表项
		toPage(list_i,li_i){
			uni.showToast({title: this.severList[list_i][li_i].name});
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
	}
}
